.catalog-list{
    z-index: 1;
    &> li{
        display: flex;
        align-items: center;
        transition: all .3s ease-out;
        padding-right: 35px;
        padding-left: 20px;
        a{
            color: var(--mainColor);
            font-size: 13px;
            flex-shrink: 1;
        }
        i{
            color: var(--Wedgewood);
            font-size: 52px;
            transition: all .3s ease-out;
            &.icon-2-9_catalog2{
                font-size: 42px;
            }
            &.icon-3-0_catalog3{
                font-size: 35px;
            }
            &.icon-3-1_catalog4{
                font-size: 48px;
            }
            &.icon-3-2_catalog5{
                font-size: 42px;
            }
            &.icon-3-3_catalog6{
                font-size: 47px;
            }
            &.icon-3-4_catalog7,  &.icon-3-5_catalog8{
                font-size: 43px;
            }
            &.icon-3-6_catalog9{
                font-size: 40px;
            }
            &.icon-3-7_catalog10{
                font-size: 20px;
            }
            &.icon-3-8_catalog11{
                font-size: 45px;
            }
            &.icon-3-9_catalog12{
                font-size: 50px;
            }
        }
        &:hover{
            background-color: var(--Wedgewood);
            transition: all .3s ease-out;
            &> .icon-wrap i, &> a{
                color: var(--White);
                transition: all .3s ease-out;
            }
        }
        .list{
            padding-top: 20px;
        }
    }
    .arrowDown{
        &::after{
            color: var(--VeryLightGrey);
            font-size: 7px;
            transform: rotate(-90deg);
            right: 16px;
        }
        &:hover{
            &::after{
                transform: rotate(-90deg);
                color: var(--Flamenco);
            }
        }
    }
    .dropdown{
        left: 100%;
        top: 0;
        z-index: 4;
    }
    li{
        padding-top: 11px;
        padding-bottom: 11px;
    }
}
.navList{
    /* width: 1170px; */
    /* width: 780px; */
    display: flex;
    &> div{
        /* width: calc(100% / 3); */
        /* width: calc(100% / 2); */
        width: 390px;
    }
    .firstColumn, .secondColumn{
        li{
            display: flex;
            &:hover{
                i{
                    color: var(--Flamenco);
                }
            }
            i{
                &.icon-4-2_catalog-list3{
                    font-size: 49px;
                }
                &.icon-4-3_catalog-list4{
                    font-size: 32px;
                }
                &.icon-4-4_catalog-list5{
                    font-size: 43px;
                }
                &.icon-4-7_catalog-list8{
                    font-size: 30px;
                }
            }
            &.item{
                padding-bottom: 22px;
                li:last-of-type{
                    padding-bottom: 0;
                }
            }
        }
    }
    .thirdColumn{
        display: none;
        padding-top: 17px;
        a.Btn-Wedgewood{
            color: var(--Wedgewood);
            &:hover{
                color: var(--White);
            }
        }
        .card-body{
            padding: 0;
        }
    }
}
li.menu-left__active {
    background-color: var(--Wedgewood);
    &>a {
        color: var(--White);
    }
    &>.icon-wrap i {
        color: var(--White);
    }
}
i.icon-4-2_catalog-list3 {
    margin-right: 18.5px;
}
i.icon-4-3_catalog-list4 {
    margin-left: 15px;
}
.vm-item:first-child:hover .vm-menu {
    top: -22px;
}
/* .vm-item:last-child:hover .vm-menu {
    bottom: -63px;
    top: auto;
} */
@media(max-width: 1600px){
    .navList{
        width: 950px;
    }
}
@media(max-width: 1380px){
    .navList{
        width: 818px;
    }
}